/* stylelint-enable */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-semiBold.woff2") format("woff2"), url("../fonts/montserrat-semiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Montserrat", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.list-style {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #f9fbfd;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(1, 28, 64, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-header {
  background-color: #00326C;
}
@media (max-width: 767px) {
  .main-header[data-header-state=no-js] {
    background-color: #f9fbfd;
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 84.9%;
  max-width: 1160px;
  min-height: 38px;
  margin: 0 auto;
  padding-top: 35px;
  outline: 1px solid red;
}
@media (max-width: 1023px) {
  .main-header__wrapper {
    min-width: 678px;
    width: 88.3%;
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    position: relative;
    min-width: 290px;
    width: 90.62%;
    padding-top: 20px;
  }
  .main-header__wrapper[data-header-state=no-js] {
    flex-direction: column;
    justify-content: flex-start;
    transition: 1.5s;
  }
  .main-header__wrapper[data-header-state=open] {
    position: relative;
    flex-direction: column;
    justify-content: flex-start;
    width: 89%;
    min-width: 285px;
    min-height: 100vh;
    margin-right: 0;
    padding: 20px 4.7% 0 15px;
    background-color: #f9fbfd;
    transition: 1.5s;
  }
}

.main-header__link {
  margin-bottom: auto;
  width: 70px;
  height: 38px;
  cursor: pointer;
}
.main-header__link svg {
  width: 100%;
  height: 100%;
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .main-header__link {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .main-header__link {
    position: relative;
    z-index: 5;
    width: 42px;
    height: 22px;
    margin-right: auto;
  }
  .main-header__link[data-header-state=no-js] svg {
    fill: #011c40;
  }
  .main-header__link[data-header-state=open] {
    margin-bottom: 0;
  }
  .main-header__link[data-header-state=open] svg {
    fill: #011c40;
  }
}
@media (hover: hover) {
  .main-header__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.main-header__link.focus-visible:focus {
  opacity: 0.6;
}

.main-header__toggler {
  display: none;
}
@media (hover: hover) {
  .main-header__toggler:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.main-header__toggler.focus-visible:focus {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .main-header__toggler {
    display: block;
    width: 24px;
    height: 14px;
    padding: 0;
    background-color: inherit;
    border: none;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
  }
  .main-header__toggler[data-header-state=closed] {
    background-image: url("../img/svg/burger.svg");
  }
  .main-header__toggler[data-header-state=open] {
    position: absolute;
    top: 25px;
    right: 4.7%;
    background-image: url("../img/svg/cross.svg");
  }
  .main-header__toggler[data-header-state=no-js] {
    display: none;
  }
}

.nav-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 80%;
  margin-left: 30px;
}
@media (max-width: 767px) {
  .nav-list {
    position: relative;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    margin: 0;
    margin-top: 33px;
  }
  .nav-list[data-header-state=closed] {
    display: none;
  }
  .nav-list[data-header-state=open] .nav-list__item {
    margin-right: 15px;
  }
}

@media (max-width: 767px) {
  .nav-list__item {
    width: 100%;
  }
}
.nav-list__item:not(:last-child) {
  margin-right: 33px;
}
@media (max-width: 1023px) {
  .nav-list__item:not(:last-child) {
    margin-right: 23px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .nav-list__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.nav-list__link {
  display: block;
  padding: 5px 0;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  color: #f9fbfd;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .nav-list__link {
    padding: 2px 0;
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .nav-list__link {
    width: 100%;
    min-height: 40px;
    padding: 10px;
    padding-bottom: 8px;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .nav-list__link:hover:not(.focus-visible) {
    color: #f9fbfd;
    background-color: #011c40;
  }
}
@media (max-width: 767px) {
  .nav-list__link.focus-visible:focus {
    color: #f9fbfd;
    background-color: #011c40;
  }
}
@media (hover: hover) {
  .nav-list__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.nav-list__link.focus-visible:focus {
  opacity: 0.6;
}

